import React from 'react';
import { GraduationCap } from 'lucide-react';

interface SemesterRow {
  id: string;
  semester: string;
  credits: number;
  gpa: number;
}

const mockSemesters: SemesterRow[] = [
  { id: '1', semester: 'Ganjil 2023/2024', credits: 21, gpa: 3.45 },
  { id: '2', semester: 'Genap 2023/2024', credits: 20, gpa: 3.60 },
  { id: '3', semester: 'Ganjil 2024/2025', credits: 18, gpa: 3.70 },
];

const totalCredits = mockSemesters.reduce((s, r) => s + r.credits, 0);
const ipk = mockSemesters.reduce((s, r) => s + r.gpa * r.credits, 0) / (totalCredits || 1);

const Transcript: React.FC = () => {
  return (
    <div className="max-w-7xl mx-auto space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold text-gray-900">Transcript</h1>
        <div className="text-sm text-gray-500">Student ID: 123456789</div>
      </div>

      {/* Summary */}
      <div className="card flex items-center justify-between">
        <div className="flex items-center space-x-3">
          <GraduationCap className="h-6 w-6 text-primary-600" />
          <div>
            <div className="text-sm text-gray-500">Total Credits</div>
            <div className="text-xl font-semibold">{totalCredits} SKS</div>
          </div>
        </div>
        <div>
          <div className="text-sm text-gray-500">Cumulative GPA (IPK)</div>
          <div className="text-xl font-semibold">{ipk.toFixed(2)}</div>
        </div>
      </div>

      {/* Semester list */}
      <div className="card overflow-x-auto">
        <table className="min-w-full text-sm">
          <thead>
            <tr className="text-left text-gray-600">
              <th className="py-2 pr-4">Semester</th>
              <th className="py-2 pr-4">Credits</th>
              <th className="py-2 pr-4">Semester GPA (IPS)</th>
            </tr>
          </thead>
          <tbody>
            {mockSemesters.map(row => (
              <tr key={row.id} className="border-t border-gray-100">
                <td className="py-2 pr-4 font-medium text-gray-900">{row.semester}</td>
                <td className="py-2 pr-4">{row.credits}</td>
                <td className="py-2 pr-4">{row.gpa.toFixed(2)}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default Transcript;
